@import url(https://fonts.googleapis.com/css?family=Lato:900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #212121;
  color: #FFFFFF;
  font-family: 'Lato', sans-serif;
}

.HeadMain{
  margin-bottom:5px;
  box-shadow: 0px 0px 4px #676767;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.Header-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40px;
  pointer-events: none;
}

.Mini-logo {
  height: 25px;
  pointer-events: none;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.navyblue-background{
  background: #20232a;
}

.leftAlign{
  text-align: left;
}

.firstSection h1{
  font-size: 20px;
  font-weight: normal;
}

.firstSection h3{
  font-size: 60px;
  font-weight: normal;
  color: #b3b3b3;
}

.firstSection h2{
  font-size: 80px;
  line-height: 1.1;
  font-weight: 900;
}

.separator{
  background-color: blue;
}

.firstParagraph{
  max-width:500px;
}

.timeline{
  display:flex;
  flex-direction: row;
}

.timeline ul,li,ol {
  list-style: none;
}

.experienceList li{
  border-left: 1px solid #3d3d3d;
  padding: 15px 20px;
}

.experienceList li:hover{
  background-color: #292929;
  cursor: pointer;
}

.experienceList li a:hover{
    color: burlywood;
}


.headerVs{
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 2;

  -webkit-animation-name: headerY; /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 1s; /* Safari 4.0 - 8.0 */
  animation-name: headerY;
  animation-duration: 1s;
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
}

hr{
  border-top: 1px solid rgba(255, 255, 255, 0.05)!important;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes headerY {
  from {-webkit-transform: translateY(-150px);transform: translateY(-150px);}
  to {-webkit-transform: translateY(0px);transform: translateY(0px);}
}

@keyframes headerY {
  from {-webkit-transform: translateY(-150px);transform: translateY(-150px);}
  to {-webkit-transform: translateY(0px);transform: translateY(0px);}
}

